import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Jumbotron, Row, Col, Image, Button, Container, DropdownButton, Dropdown, FormControl } from 'react-bootstrap';
import './Home.css';


//---------------------------------------Custom Toggle ----------------------------------------------
const CustomToggle = React.forwardRef
    (
        ({ children, onClick }, ref) =>
            (
                <a href="" ref={ref} onClick={e => { e.preventDefault(); onClick(e); }} >
                    {children} &#x25bc;
                        </a>
            )
    );


const CustomMenu = React.forwardRef(
    ({ children, style, className, 'aria-labelledby': labeledBy }, ref) => {
        const [value, setValue] = React.useState('');

        return (
            <div
                ref={ref}
                style={style}
                className={className}
                aria-labelledby={labeledBy}
            >
                <FormControl
                    autoFocus
                    className="mx-3 my-2 w-auto"
                    placeholder="Type to filter..."
                    onChange={e => setValue(e.target.value)}
                    value={value}
                />
                <ul className="list-unstyled">
                    {React.Children.toArray(children).filter(
                        child =>
                            !value || child.props.children.toLowerCase().startsWith(value),
                    )}
                </ul>
            </div>
        );
    },
);
//----------------------------------------------------- Custom Toggle -------------------------------------------------------




export default class Home extends Component {
    render() {
        return (

            //<div className="Home">
            //    <h2 className="App-header"> testing </h2>
            //    </div>
            <Container>
                <Jumbotron>
                    <h2> Hello hello.. testing testing </h2>
                    <p> Poo Poo Lin is stupid </p>
                    <Link to="/about">
                        <Button variant="outline-primary">About</Button> {' '}
                    </Link>  
                </Jumbotron>
                
                   
                <Dropdown>
                    <Dropdown.Toggle bsStyle='primary' title='Primary'> Select
                    </Dropdown.Toggle>
                    <Dropdown.Menu>
                        <Dropdown.Item href="/About">About</Dropdown.Item>
                        <Dropdown.Item eventKey='2'>Another action</Dropdown.Item>
                        <Dropdown.Item eventKey='3' active={true}>Active Item</Dropdown.Item>
                        <Dropdown.Item divider />
                        <Dropdown.Item eventKey='4'>Separated link</Dropdown.Item>
                        </Dropdown.Menu>
                </Dropdown>

                
            
            <br></br>
                <Dropdown>
                    <Dropdown.Toggle as={CustomToggle} id="dropdown-custom-components">
                        Custom toggle
                    </Dropdown.Toggle>

                    <Dropdown.Menu as={CustomMenu}>
                        <Dropdown.Item eventKey="1">Red</Dropdown.Item>
                        <Dropdown.Item eventKey="2">Blue</Dropdown.Item>
                        <Dropdown.Item eventKey="3" active>Orange</Dropdown.Item>
                        <Dropdown.Item eventKey="1">Red-Orange</Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>
              
            </Container>
        )

    }
}

